header {
    width: 100%;
    height: 70px;
    background-color: #202020;
    display: flex;

    &>div {
        width: calc(100%/3);
    }

    .logo {
        position: relative;
        color: #fff;
        margin: 0;
        height: 70px;
        line-height: 70px;
        margin-left: 30px;
        font-size: 35px;
        display: flex;
        align-items: center;
        cursor: pointer;

        div {
            height: 40px;
            margin: 0 5px;
            background-color: rgb(100, 100, 100);
            display: flex;
            align-items: center;
            font-size: 24px;
            padding: 0 10px;
            font-weight: 700;
            border-radius: 10px;
            color: #b6b6b6;
        }

        span {
            font-size: 25px;
            font-weight: 900;
            color: rgb(100, 100, 100);
        }

        div.main {
            padding-bottom: 5px;
        }

        span:nth-child(3) {
            transform: scaleX(-1);
        }
    }

    .search-bar {
        display: flex;
        justify-content: center;
        align-items: center;

        form {
            width: 80%;
        
            input {
                background-color: #464646;
                padding: 10px 20px;
                height: 40px;
                width: 100%;
                border-radius: 10px;
                font-size: 20px;
                border: none;
                outline: none;
                color: rgb(199, 199, 199);

                &::before {
                    content: 'XD';
                }
            }
        }
    }
}